/* Define Material Design 3 color tokens */
:root {
  --md-sys-color-primary: #6200ee; /* Primary button color */
  --md-sys-color-on-primary: #ffffff; /* Text color on primary buttons */
  --md-sys-color-secondary: #03dac6; /* Secondary button color */
  --md-sys-color-on-secondary: #000000; /* Text color on secondary buttons */
  --md-sys-color-surface: #ffffff; /* Background color */
  --md-sys-color-on-surface: #000000; /* Text color */
}

/* General styling */
body {
  font-family: 'Roboto', Arial, sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #f6f6f6;
}

header {
  background-color: #004aad;
  color: white;
  padding: 20px;
}

nav {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
}

footer {
  margin-top: 20px;
  padding: 10px;
  background-color: #eeeeee;
  color: #666666;
}
